/**
 * @fileOverview Custom error classes for the application.
 */

/**
 * Represents an error related to geolocation operations.
 * Includes the original error code from the Geolocation API.
 */
export class GeolocationError extends Error {
  /**
   * The error code from the Geolocation API (e.g., 1 for PERMISSION_DENIED).
   */
  code: number;

  /**
   * Creates an instance of GeolocationError.
   * @param message - The error message.
   * @param code - The Geolocation API error code.
   */
  constructor(message: string, code: number) {
    super(message);
    this.name = 'GeolocationError';
    this.code = code;
    // Set the prototype explicitly.
    Object.setPrototypeOf(this, GeolocationError.prototype);
  }
}
