'use client';

import type { ChangeEvent } from 'react';
import { useState, useEffect, useRef } from 'react'; // Added useRef
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Alert, AlertTitle, AlertDescription } from '@/components/ui/alert'; // Added Alert components
import { useToast } from '@/hooks/use-toast';
import { getCurrentLocation } from '@/services/geo-location';
// import { captureSelfie as captureSelfieService } from '@/services/camera'; // Using inline implementation for now
import type { GeoLocation, AttendanceRecord } from '@/lib/types';
import { GeolocationError } from '@/lib/errors'; // Import the custom error
import Image from 'next/image';
import { Camera, MapPin, Send, Loader2, User, Video, AlertTriangle } from 'lucide-react'; // Added Video and AlertTriangle

// Mock function to simulate saving attendance
async function saveAttendance(data: Omit<AttendanceRecord, 'id' | 'timestamp'>): Promise<AttendanceRecord> {
  console.log('Saving attendance:', data);
  // Simulate API call delay
  await new Promise(resolve => setTimeout(resolve, 1500));
  const record: AttendanceRecord = {
    ...data,
    id: Math.random().toString(36).substring(7),
    timestamp: new Date(),
  };

   // Add to local storage for history page simulation
   try {
        const history = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
        history.push(record); // Add the new record
        // Keep only the latest 100 records for performance
        const updatedHistory = history.slice(-100);
        localStorage.setItem('attendanceHistory', JSON.stringify(updatedHistory));
   } catch (e) {
       console.error("Failed to update attendance history in localStorage:", e);
       // Decide if this should prevent the success toast or not
   }

  return record;
}

const attendanceSchema = z.object({
  userId: z.string().min(1, 'User ID is required'),
  userName: z.string().min(1, 'User Name is required'),
  selfie: z.string().min(1, 'Selfie is required (click Capture Selfie).').url('Selfie must be a valid data URL.'), // Validate it's a data URL
  location: z.object({
    latitude: z.number(),
    longitude: z.number(),
  }), // Location is now required and set by fetchLocation
});

type AttendanceFormData = z.infer<typeof attendanceSchema>;

export default function AttendancePage() {
  const { toast } = useToast();
  const [location, setLocation] = useState<GeoLocation | null>(null);
  const [locationError, setLocationError] = useState<string | null>(null);
  const [selfiePreview, setSelfiePreview] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isCapturingSelfie, setIsCapturingSelfie] = useState(false);
  const [isFetchingLocation, setIsFetchingLocation] = useState(false);
  const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null); // null = checking, true = granted, false = denied/error
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null); // Ref for canvas element

  const form = useForm<AttendanceFormData>({
    resolver: zodResolver(attendanceSchema),
    defaultValues: {
      userId: '',
      userName: '',
      selfie: '',
      // location is set dynamically, so no default needed here explicitly,
      // but the schema requires it for validation before submission.
    },
     // Ensure validation happens correctly for dynamically set fields
    mode: 'onChange',
  });

  // Request camera permission and set up video stream
 useEffect(() => {
    let stream: MediaStream | null = null;

    const getCameraPermission = async () => {
      setHasCameraPermission(null); // Indicate checking status
      try {
        stream = await navigator.mediaDevices.getUserMedia({
            video: {
              facingMode: 'user', // Prefer front camera
              width: { ideal: 640 }, // Slightly higher resolution
              height: { ideal: 480 }
            },
            audio: false
        });
        setHasCameraPermission(true);

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        }
      } catch (error: any) {
        console.error('Error accessing camera:', error);
        setHasCameraPermission(false);
        let message = 'Could not access the camera.';
        if (error.name === 'NotAllowedError' || error.name === 'PermissionDeniedError') {
           message = 'Camera permission denied. Please allow camera access in your browser settings.';
        } else if (error.name === 'NotFoundError' || error.name === 'DevicesNotFoundError') {
            message = 'No camera found. Please ensure a camera is connected and enabled.';
        } else if (error.name === 'NotReadableError' || error.name === 'TrackStartError') {
            message = 'Camera is already in use or cannot be accessed.';
        }
        toast({
          variant: 'destructive',
          title: 'Camera Access Error',
          description: message,
        });
      }
    };

    getCameraPermission();

    // Cleanup function to stop video tracks when component unmounts
    return () => {
      stream?.getTracks().forEach(track => track.stop());
       if (videoRef.current && videoRef.current.srcObject) {
         (videoRef.current.srcObject as MediaStream).getTracks().forEach(track => track.stop());
         videoRef.current.srcObject = null;
       }
    };
  }, [toast]); // Add toast dependency


  const fetchLocation = async () => {
    setIsFetchingLocation(true);
    setLocationError(null);
    setLocation(null); // Reset location state
    form.setValue('location', undefined as any, { shouldValidate: true }); // Reset form value and trigger validation
    try {
      const currentLocation = await getCurrentLocation();
      setLocation(currentLocation);
      form.setValue('location', currentLocation, { shouldValidate: true }); // Set location in form and validate
      console.log('Location fetched:', currentLocation);
      // Clear previous errors if successful
      setLocationError(null);
    } catch (error: any) {
      console.error('Error fetching location:', error);
       // Handle specific GeolocationError
      if (error instanceof GeolocationError) {
           setLocationError(error.message); // Use the detailed message from the custom error
           if (error.code === 1) { // PERMISSION_DENIED
               console.warn("Geolocation permission denied by user."); // Log permission denial as warning
           }
           toast({
               variant: 'destructive',
               title: 'Location Error',
               description: error.message, // Show specific error to user
           });
      } else {
          // Handle generic errors
          const fallbackMessage = 'Could not retrieve your current location. Please try again.';
          setLocationError(fallbackMessage);
          toast({
            variant: 'destructive',
            title: 'Location Error',
            description: fallbackMessage,
          });
      }
      setLocation(null); // Ensure location is null on error
      form.setValue('location', undefined as any, { shouldValidate: true }); // Reset form value
    } finally {
      setIsFetchingLocation(false);
    }
  };

  // Fetch location on component mount
  useEffect(() => {
    fetchLocation();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Run only once on mount


  const handleCaptureSelfie = () => {
    if (!videoRef.current || !canvasRef.current || !hasCameraPermission) {
        toast({
            variant: 'destructive',
            title: 'Camera Not Ready',
            description: hasCameraPermission === false
              ? 'Camera access was denied or failed. Please check permissions.'
              : 'Camera feed is not available.',
          });
      return;
    }

    setIsCapturingSelfie(true);
    const video = videoRef.current;
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    if (context) {
        // Set canvas dimensions to match video element (which should match stream)
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;

        // Draw the current video frame onto the canvas
        context.drawImage(video, 0, 0, canvas.width, canvas.height);

        // Get the image data from the canvas
        const dataUrl = canvas.toDataURL('image/png');

        setSelfiePreview(dataUrl);
        form.setValue('selfie', dataUrl, { shouldValidate: true });
        console.log('Selfie captured');
        toast({
            title: 'Selfie Captured!',
            description: 'Selfie is ready for submission.',
          });
    } else {
        console.error('Failed to get canvas context');
        toast({
            variant: 'destructive',
            title: 'Selfie Error',
            description: 'Could not capture selfie due to a technical issue.',
          });
    }
    setIsCapturingSelfie(false); // Ensure this runs even if context fails
  };


  const onSubmit = async (data: AttendanceFormData) => {
     // Re-validate location just before submission, although schema should catch it
    if (!location) {
      toast({
        variant: 'destructive',
        title: 'Missing Location',
        description: 'Location data is missing. Please ensure location is fetched.',
      });
       // Attempt to fetch location again if missing
      fetchLocation();
      return;
    }

     // Check selfie again, although schema validation should handle this
     if (!data.selfie || !selfiePreview) {
       toast({
           variant: 'destructive',
           title: 'Missing Selfie',
           description: 'Please capture a selfie before submitting.',
         });
       return;
   }

    setIsSubmitting(true);
    console.log('Submitting attendance data:', data);

    try {
      // Ensure the location in the data matches the state (should be guaranteed by form.setValue)
      const attendanceDataToSave: Omit<AttendanceRecord, 'id' | 'timestamp'> = {
        userId: data.userId,
        userName: data.userName,
        selfie: data.selfie,
        location: data.location, // Use validated location from form data
      };

      const savedRecord = await saveAttendance(attendanceDataToSave);

      toast({
        title: 'Attendance Submitted',
        description: `Attendance marked successfully at ${new Date(savedRecord.timestamp).toLocaleTimeString()}.`,
      });

      // Reset form, selfie preview, but keep location unless it needs refresh
      form.reset({ userId: '', userName: '', selfie: '', location: location }); // Keep location after successful submit
      setSelfiePreview(null);

      // Consider if location should be refetched after submission
      // fetchLocation();

    } catch (error) {
      console.error('Error saving attendance:', error);
      toast({
        variant: 'destructive',
        title: 'Submission Failed',
        description: 'Could not save your attendance. Please try again.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="flex justify-center items-center">
      <Card className="w-full max-w-md shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl font-bold text-center text-primary">Mark Attendance</CardTitle>
          <CardDescription className="text-center">Submit your attendance with a selfie and current location.</CardDescription>
        </CardHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)}>
            <CardContent className="space-y-6">
               <FormField
                control={form.control}
                name="userId"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-1"><User className="h-4 w-4" /> User ID</FormLabel>
                    <FormControl>
                      <Input placeholder="Enter your User ID" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="userName"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-1"><User className="h-4 w-4" /> User Name</FormLabel>
                    <FormControl>
                      <Input placeholder="Enter your Name" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Selfie Section */}
               <FormField
                control={form.control}
                name="selfie"
                render={({ field }) => ( // field is available but we manually update with setValue
                    <FormItem>
                         <FormLabel className="flex items-center gap-1"><Camera className="h-4 w-4" /> Selfie</FormLabel>
                         <div className="space-y-2">
                           {/* Video Feed */}
                           <div className="relative w-full aspect-video bg-muted rounded-md overflow-hidden border">
                             <video ref={videoRef} className="w-full h-full object-cover" autoPlay muted playsInline />
                             {/* Overlay for permission status */}
                             {hasCameraPermission === false && (
                               <div className="absolute inset-0 flex items-center justify-center bg-black/50 text-destructive-foreground p-4">
                                 <div className="text-center">
                                   <AlertTriangle className="h-8 w-8 mx-auto mb-2" />
                                   <p>Camera access denied or unavailable.</p>
                                   <p className="text-xs mt-1">Check browser/system settings.</p>
                                 </div>
                               </div>
                             )}
                              {hasCameraPermission === null && (
                               <div className="absolute inset-0 flex items-center justify-center bg-black/50 text-primary-foreground">
                                 <Loader2 className="h-8 w-8 animate-spin" />
                               </div>
                             )}
                           </div>

                            {/* Hidden Canvas for Capturing Frame */}
                            <canvas ref={canvasRef} style={{ display: 'none' }}></canvas>


                             {/* Capture Button and Preview */}
                            <div className="flex items-center gap-4">
                                 <Button
                                    type="button"
                                    variant="outline"
                                    onClick={handleCaptureSelfie}
                                    disabled={isCapturingSelfie || isSubmitting || hasCameraPermission !== true}
                                    className="flex-shrink-0"
                                 >
                                    {isCapturingSelfie ? (
                                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                                    ) : (
                                        <Video className="mr-2 h-4 w-4" /> // Use Video icon
                                    )}
                                    Capture Selfie
                                </Button>
                                {selfiePreview && (
                                <div className="w-20 h-20 rounded-md overflow-hidden border border-muted shrink-0">
                                    <Image
                                    src={selfiePreview}
                                    alt="Selfie Preview"
                                    width={80}
                                    height={80}
                                    className="object-cover w-full h-full"
                                    />
                                </div>
                                 )}
                            </div>
                             {/* Display camera permission alert if needed */}
                             {hasCameraPermission === false && (
                                <Alert variant="destructive" className="mt-2">
                                     <AlertTriangle className="h-4 w-4" />
                                      <AlertTitle>Camera Access Issue</AlertTitle>
                                      <AlertDescription>
                                        Could not access the camera. Please ensure permissions are granted and no other app is using it.
                                      </AlertDescription>
                                </Alert>
                             )}
                         </div>
                         <FormMessage /> {/* Shows validation errors for the selfie field */}
                    </FormItem>
                 )}
                />


              {/* Location Section */}
              <div className="space-y-2">
                <Label className="flex items-center gap-1"><MapPin className="h-4 w-4" /> Location</Label>
                <div className="p-3 bg-secondary rounded-md border text-sm min-h-[40px] flex items-center">
                  {isFetchingLocation ? (
                     <div className="flex items-center text-muted-foreground">
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Fetching location...
                     </div>
                  ) : location ? (
                    <p>
                      Lat: {location.latitude.toFixed(5)}, Lon: {location.longitude.toFixed(5)}
                    </p>
                  ) : (
                    <p className="text-destructive">{locationError || 'Location not available.'}</p>
                  )}
                </div>
                 {/* Show retry button only if there was an error and not currently fetching */}
                {locationError && !isFetchingLocation && (
                    <Button type="button" variant="outline" size="sm" onClick={fetchLocation} disabled={isFetchingLocation}>
                        <MapPin className="mr-2 h-4 w-4" />
                        Retry Location
                    </Button>
                )}
                {/* Display validation errors for the location field */}
                <FormMessage>{form.formState.errors.location?.message}</FormMessage>
              </div>
            </CardContent>
            <CardFooter>
              <Button
                type="submit"
                className="w-full bg-accent hover:bg-accent/90"
                disabled={
                    isSubmitting ||
                    isFetchingLocation ||
                    isCapturingSelfie ||
                    hasCameraPermission !== true || // Disable if no camera permission
                    !location || // Disable if no location
                    !selfiePreview || // Disable if no selfie preview (implies selfie field is likely empty)
                    !form.formState.isValid // Disable if form is invalid
                 }
              >
                {isSubmitting ? (
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                ) : (
                  <Send className="mr-2 h-4 w-4" />
                )}
                Submit Attendance
              </Button>
            </CardFooter>
          </form>
        </Form>
      </Card>
    </div>
  );
}
